import React from 'react';
import ReactDOM from 'react-dom';
import './Header.scss';
import 'bootstrap/dist/css/bootstrap.css';

import background1 from '../assets/bg-1.png';
import logo from '../assets/droplet-logo.png';
import logoDark from '../assets/droplet-logo-dark.png';
import logOutImg from '../assets/logout-icon.png';

class Header extends React.Component{
  constructor(props){
    super(props);
  }

  homeHeader(){
    return(
      <div className="header-section sticky-header" style={{backgroundImage: `url(${background1})`}}>
        <div className="header-left">
            <img src={logo} className="droplet-logo" onClick={this.props.isHome? '' : this.props.goHome }/>
        </div>
        {this.homeHeaderRightSection()}
      </div>
    );
  }

  otherHeader(){
    return(
      <div className="header-section" >
        <div className="header-left">
            <img src={logoDark} className="droplet-logo" onClick={this.props.goHome }/>
        </div>
        {this.headerRightSection()}
      </div>
    );
  }

  homeHeaderRightSection(){
    if(this.props.isLoggedIn){
      return(
        <div className="header-right">
            <label className="header-text clickable">Welcome, Elson Ng</label>

            <button className="btn btn-buy-now" onClick={this.props.buyNow}>BUY NOW</button>
        </div>
    );
    }
    else{
      return(
        <div className="header-right-session">
          <label className="header-text pointer" onClick={this.props.instructions}>How it Works</label>
          <label className="header-text pointer" onClick={this.props.login}>Login</label>
          <button className="btn btn-buy-now" onClick={this.props.buyNow}>BUY NOW</button>
        </div>
    );
    }
  }

  headerRightSection(){
    if(this.props.isLoggedIn){
      return(
        <div className="header-right">
            <label className="name-text clickable">Welcome, Elson Ng</label>
            <div className="slash-div"></div>
            <label className="name-text clickable">Log out</label>

            <img src={logOutImg} className="logout-img clickable"/>
        </div>
      );
      }
    else{
      const loginClass = this.props.isHome ? 'header-text pointer' : 'header-text-dark pointer';
      return(
        <div className="header-right">
          <label className={loginClass} onClick={this.props.goToLogin}>Login</label>
        </div>
      );
    }
  }

  render(){
    return(
      <div>
        {this.props.isHome ? this.homeHeader(): this.otherHeader()}
        </div>
    );
  }
}

export default Header;



